import { NextResponse } from "next/server";
import type { NexflowDiscoveredData, NexflowAction } from "@/lib/nexflow-types";
import discoveredData from "../../../../../data/nexflow-discovered.json";

export type NexflowProvider = {
  domain: string;
  actionCount: number;
  actions: NexflowAction[];
};

export async function GET() {
  try {
    const data = discoveredData as NexflowDiscoveredData;

    // Group actions by domain
    const providerMap = new Map<string, NexflowAction[]>();

    for (const action of data.actions) {
      const existing = providerMap.get(action.domain) || [];
      existing.push(action);
      providerMap.set(action.domain, existing);
    }

    const providers: NexflowProvider[] = Array.from(providerMap.entries()).map(
      ([domain, actions]) => ({
        domain,
        actionCount: actions.length,
        actions,
      })
    );

    return NextResponse.json({
      crawledAt: data.crawledAt,
      providerCount: providers.length,
      providers,
    });
  } catch (error) {
    console.error("Failed to load providers:", error);
    return NextResponse.json(
      { error: "Failed to load providers" },
      { status: 500 }
    );
  }
}

